import React, { useState } from "react";
import { motion } from "framer-motion";
import { useMediaQuery } from "react-responsive";

import "./nav-mobile.scss";

import CNLogo from "../../assets/Chelize Nell Logo - Mobile.svg";

const containerVariants = {
  hidden: {
    opacity: 0,
  },
  visible: {
    opacity: 1,

    transition: {
      staggerChildren: 0.1,
      when: "beforeChildren",
    },
  },
};

const childVariants = {
  hidden: {
    opacity: 0,
    y: 50,
  },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.6,
      ease: "easeInOut",
    },
  },
};

const NavMobile = () => {
  const [toggleNav, setToggleNav] = useState(false);
  const isMobile = useMediaQuery({ query: `(max-width: 850px)` });

  const handleLink = () => {
    setTimeout(() => {
      setToggleNav(!toggleNav);
    }, 800);
  };

  return (
    <>
      <nav className={`nav-mobile ${toggleNav && "nav-visible"}`}>
        <div className="logo">
          <img src={CNLogo} alt="" />
        </div>
        <div
          id="mobileToggler"
          className={`${toggleNav && "active"}`}
          onClick={() => setToggleNav(!toggleNav)}
        >
          <span className="bar"></span>
          <span className="bar"></span>
        </div>
      </nav>

      {toggleNav && (
        <motion.ul
          className="navbar"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.3, ease: "easeInOut" }}
        >
          <motion.div
            className="container"
            variants={containerVariants}
            initial="hidden"
            animate="visible"
          >
            <motion.li className="navbar-item" variants={childVariants}>
              <a
                href="#"
                className="navbar-item_link"
                onClick={isMobile && handleLink}
              >
                Home
              </a>
            </motion.li>
            <motion.li className="navbar-item" variants={childVariants}>
              <a
                href="#"
                className="navbar-item_link"
                onClick={isMobile && handleLink}
              >
                Portfolio
              </a>
            </motion.li>
            <motion.li className="navbar-item" variants={childVariants}>
              <a
                href="#"
                className="navbar-item_link"
                onClick={isMobile && handleLink}
              >
                About Me
              </a>
            </motion.li>
            <motion.li
              className="navbar-item"
              variants={childVariants}
              onClick={isMobile && handleLink}
            >
              <a href="#" className="navbar-item_link">
                Contact
              </a>
            </motion.li>
          </motion.div>
        </motion.ul>
      )}
    </>
  );
};

export default NavMobile;
